var _self
export default {
	data() {
		return {
			phoneReg: /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/,
			user: {}
		}
	},
	methods: {
		radioChange(e){
			_self = this;
			_self.user.usex = e.target.value;
		},
		toGo(){
			_self = this;
			var userid = _self.user.userid;
			var phone = _self.user.uphonenum;
			var uusername = _self.user.uusername;
			var uaddress = _self.user.uaddress;
			var usex = _self.user.usex;
			if(uusername != "" && uusername != null){
				if(phone != "" && phone != null){
					if(_self.phoneReg.test(phone) && phone.length == 11){
						if(uaddress != "" && uaddress != null){
						uni.request({
							url: _self.apiServer + 'mall/user/updateUser',
							method: 'POST',
							header: { 'content-type': 'application/x-www-form-urlencoded' },
							data: {
								userid: userid,
								uphoneNum: phone,
								uusername: uusername,
								uaddress: uaddress,
								usex: usex
							},
							success: res => {
								console.log(res);
								console.log(res.data.status);
								if(res.data.status == 2122){
									uni.showToast({
										title: '该昵称不可用',
										icon: 'none'
									});
								}else{
									uni.showModal({
										title: '提交成功',
										success: function (res) {
										        if (res.confirm) {
										            uni.reLaunch({ url: '/pages/my/my' });
										        } else if (res.cancel) {
										            console.log("用户点击取消")
										        }
										    }})
									
								}
							},
							fail: e => {
								console.log(e);
							},
							complete: () => {}
						})
						}else{
							uni.showToast({
								title: '地址不能为空',
								icon: 'none'
							});
						}
					}else{
						uni.showToast({
							title: '手机号格式不对',
							icon: 'none'
						});
					}
				}else{
					uni.showToast({
						 title: '手机号不能为空',
						 icon: 'none'
					})
				}
			}else{
				uni.showToast({
					 title: '昵称不能为空',
					 icon: 'none'
				})
			}
		}
		
	},
	onLoad(item) {
		_self = this;
		_self.user = JSON.parse(item.user);
	}

}
